<template>
  <div class="menu-wrapper">
    <h1>НАТАЛЬНАЯ БАРНАЯ&nbsp;КАРТА</h1>
    <p class="cocktail__note">Нажми на название коктейля, чтобы узнать его рецепт</p>
    <div v-for="cocktail in cocktails" :key="cocktail.id" class="cocktail">
      <div class="cocktail__title">
        <p @click="toggleRecipe(cocktail)">{{ cocktail.title.toLocaleUpperCase() }}</p>
        <img :src="cocktail.img">
      </div>
      <ul class="cocktail__ingredients">
        <li v-for="(ingredient, index) in cocktail.ingredients" :key="index">
          <span class="">{{ ingredient.measure }}</span> {{ ingredient.name }}
        </li>
      </ul>
      <ol class="cocktail__recipe" :class="{ active: cocktail.isVisible }">
        <li v-for="recipe in cocktail.recipe" :key="recipe.step">{{ recipe.step }}</li>
      </ol>
    </div>
  </div>
</template>

<script>
import cocktails from '../assets/resources/cocktails'
export default {
  name: 'MenuComponent',
  data() {
    return {
      cocktails,
    }
  },
  methods: {
    toggleRecipe(cocktail) {
      console.log(cocktail.isVisible)
      cocktail.isVisible = !cocktail.isVisible
    },
  },
}
</script>

