  width: 75%;
  margin: 20px auto;
	background: rgba(255, 255, 255, 0.75);
  padding: 40px 20px;
  position: relative;
  z-index: 100;
}
.cocktail {
  padding: 10px 0;
  margin: 10px 0;
  &__title {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    white-space: nowrap;
    padding-bottom: 12px;
    p {
      font-family: 'Rothek ExtraLight';
      font-size: 20px;
      font-weight: 600;
    }
    img {
      width: 36px;
    }
  }
  &__note {
    font-family: 'Rothek ExtraLight';
    font-size: 14px;
